svg { 
  width: 100%;
  height: 100%;
}

.circle {
	// background-image: url('pattern.svg');
	width: min(100vw, 190vh);
	height: min(95vw, 190vh);
	background-color: #ffc000;
	border-radius: 100%;
	margin: 0vh 0 0 -50vw;
	position: absolute;
	left: 0;
	top: -5vh;
	z-index: 1;
	animation: circle 90s infinite linear;
}

@keyframes circle {
	0% {
		transform: rotate(0);
		// background-color: #8b9ef4;

	}

	100% {
		transform: rotate(360deg);
		// background-color: #5f6ca9;

	}
}

@keyframes circle-color {
	0% {
		background-color: #005165;
	}

	50% {
		background-color: #ffc000;

	}

	100% {
		background-color: #005165;

	}
}


.triangle {
	top: 0;
	right: 0;
	position: absolute;
	// padding: 0px 100px;
	z-index: 0;
	border-style: solid;
	border-width: 0px 0px 100vh 80vw;
	border-color: transparent transparent #b1b2ff transparent;
	overflow: hidden;
}